.section {
	width: 100%;
	padding: 22px;
 }
 
 .list {
	margin-top: 24px;
	display: flex;
	column-gap: 20px;
 }
 
 .item {
	width: calc(100% / 5);
	overflow: hidden;
	text-align: center;
 }
 
 .image {
	width: 100%;
	height: 230px;
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
 }
 
 .title {
	font-weight: 600;
	font-size: 20px;
	line-height: 24px;
	margin-top: 20px;
 }